  border: 0.25rem solid #fff;
  width: 1.25rem;
}
.pulse-css:after, .pulse-css:before {
  content: "";
  width: 1rem;
  height: 1rem;
  border-radius: 0.5rem;
  position: absolute;
  top: 0;
  right: 0;
