/* Theme Toggle Container */
.header-menu-layout {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  gap: 15px;
  border-radius: 20px;
  background-color: var(--header-bg, #f0f0f0);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}
[data-theme-version=dark][data-primary=color_2] .header-menu-layout {--header-bg:#181A20}
[data-theme-version=light][data-primary=color_2]  .header-menu-layout {--header-bg:#f0f0f0}
[data-primary=color_2] .header-menu-layout button:hover  {opacity:0.8;--rgba-primary-1:var(--header-bg)} 
/* Toggle Icons */
.header-menu-layout .bn-svg {
  width: 24px;
  height: 24px;
  cursor: pointer;
  fill:currentColor;
  color: var(--icon-color, #555);
  transition: fill 0.3s ease;
}
[data-theme-version=dark][data-primary=color_2]  .header-menu-layout .bn-svg{--icon-color:#eaecef}
[data-theme-version=light][data-primary=color_2]  .header-menu-layout .bn-svg{--icon-color:#555}
.header-menu-layout .bn-svg:hover {
  fill:currentColor;
  color: var(--icon-hover-color, #222);
}
[data-theme-version=dark][data-primary=color_2]  .header-menu-layout .bn-svg:hover {--icon-hover-color:#f0b90b}
[data-theme-version=light][data-primary=color_2]  .header-menu-layout .bn-svg:hover {--icon-hover-color:#f0b90b}

